export default class ArtyomAssistant {
    constructor() {
        this.artyom = new Artyom();
        this.isListening = false;
    }

    // Add a voice command
    addCommand(command, callback) {
        this.artyom.addCommands({
            indexes: [command],
            action: callback,
        });
    }

    // Start Artyom
    startListening() {
        if (this.isListening) return;

        this.artyom.initialize({
            lang: "en-US",
            continuous: true,
            listen: true,
            debug: true,
        }).then(() => {
            console.log("Artyom is now listening...");
            this.isListening = true;
        }).catch(err => console.error("Artyom initialization failed:", err));
    }

    // Stop Artyom
    stopListening() {
        this.artyom.fatality();
        this.isListening = false;
        console.log("Artyom stopped listening.");
    }

    // Toggle Artyom listening
    toggleListening() {
        this.isListening ? this.stopListening() : this.startListening();
    }
}

